.product {
  display: flex;
  flex-direction: column;
  background-color: white;
  margin: 15px;
  padding: 15px;
  width: 100%;
}

.product > img {
  width: 100%;
  height: 200px;
  object-fit: contain;
  /* max-width: 200px; */
}

.product__button {
  width: max-content;
  margin: 0 auto;
  margin-top: 10px;
  background-color: goldenrod;
  border: 1px solid;
}
